}


#editprojectform{
    width: 720px;
    display: flex;
    flex-direction: row;
}

#editprojectform #projectdescription{
    display: flex;
    flex-direction: column;
    gap: 5px;
}

#editprojectform #projectsidebar{
    display: flex;
    flex-direction: column;
}


#editprojectform .newprojectlabel, #editprojectform .colorway{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 412px;
}

#editprojectform .newprojectlabel label{
    min-width: 120px;
}

#editprojectform .colorway label{
    min-width: 128px;
}

#editprojectform .newprojectlabel input{
