}

#editprojectform .colorway input{
    width: 150px;
}


.craftselect{
    display: flex;
    flex-direction: row;
    /* justify-content: flex-start; */
    width: 412px;
    gap: 8px;
    align-items: center;
}

.craftselect label{
    min-width: 120px;
}

#selectcategory{
    list-style: none;
    margin: 0px 0px 0px 124px;
    padding-left: 5px;
    width: 280px;
}

#selectcategory ul{
    list-style: none;
    padding: 5px;
}

.item::before{
    content: "â–¡ ";
    font-size: 20px;
}

.folder::before{
    content: "";
    background: url("../images/folder.svg");
    width: 16px;
    height: 16px;
    padding-right: 2px;
    display: inline-block;
    background-repeat: no-repeat;
}

li.subfolder::before, li.subsubfolder::before{
    content: "";
    background: url("../images/folder.svg");
    width: 16px;
    height: 16px;
    padding-right: 2px;
    display: inline-block;
    background-repeat: no-repeat;
}


#needlesandgauge{
